<template>
    <div>
        <!--<van-button type="danger" size="large" class="btn" @click="showFn" :btnTxt="maskShow.btnTxt">{{btnTxt}}</van-button>-->
        <div id="mask" v-if="maskShow.show">
            <div class="con">
                <img v-if="defaultShow && !('msgCon' in maskShow)" src="../images/gantanhao.png">
                <p class="title" v-if="defaultShow && ('title' in maskShow)">{{title}}</p>
                <ul v-if="defaultShow">
                    <li v-text="msgCon"></li>
                    <li style="color:#f03232;margin-top:10px;" v-show="maskShow.btnDoub && !('msgCon' in maskShow)"></li>
                </ul>
                <ul v-if="!defaultShow" style="margin-top:10px;">
                    <li></li>
                    <li class="red" style="color:#999;margin-top:10px;" v-show="maskShow.btnDoub">{{overdue}}</li>
                </ul>
                <div class="foot-btn" v-show="maskShow.btnDoub">
                    <button class="border" @click="closed" v-text="maskShow.cancel"></button>
                    <button @click="triggerPa" class="red" v-text="maskShow.confirmTxt" :class="{green:greenFlag}"></button>
                </div>
                <div class="foot-btn" v-show="!maskShow.btnDoub">
                    <button class="sizeBtn" @click="closed" v-text="maskShow.cancel"></button>
                </div>
                <span class="closeIcon" @click="closed">
                    <img src="../images/closed.png">
                </span>
            </div>
        </div>
    </div>
</template>
<script>
    import Vue from'vue'
    import { Button } from 'vant';
    Vue.component(Button.name, Button);
    let txt ='';
    export default {
        data () {
            return {
                btnTxt:'btnTxt' in this.$props.maskShow ? this.$props.maskShow.btnTxt : '',
                msgCon: '',
                greenFlag:'greenFlag'  in this.$props.maskShow ?  this.$props.maskShow.greenFlag : false,
                btnDoub: 'btnDoub'  in this.$props.maskShow ? this.$props.maskShow.btnDoub : true ,//
                defaultShow: 'defaultShow' in this.$props.maskShow ? this.$props.maskShow.defaultShow :true,//
                overdue: 'overdue' in this.$props.maskShow ?  this.$props.maskShow.overdue : 0,
                title:''
            }
        },
        beforeUpdate(){
            this.msgCon = 'msgCon' in this.$props.maskShow ? this.$props.maskShow.msgCon : txt;
            this.title = 'title' in this.$props.maskShow ? this.$props.maskShow.title : '';
        },
        methods : {
            closed (e) { //
                let ev = e || window.event;
                //console.log(ev.srcElement.nodeName)
                if(ev.srcElement.nodeName=='BUTTON'){//img
                    this.$emit('closed',this.$props.maskShow.cancel) //txt
                }else{
                    this.$emit('closed','')
                }
            },
            triggerPa () { // 
                this.$emit('triggerPa',this.$props.maskShow.confirmTxt)
            }
        },
        mounted () {
            this.$nextTick(function () {
                this.msgCon = 'msgCon' in this.$props.maskShow ? this.$props.maskShow.msgCon : txt;
                this.title = 'title' in this.$props.maskShow ? this.$props.maskShow.title : '';
               let str = this.$props.maskShow.confirmTxt;
               if(str ==''){
                   this.greenFlag = '123'
               }
            })
        },
        computed : {

        },
        props : {
            maskShow : {
                type: Object,
                default: () => {
                    return {
                        confirmTxt:'', //
                        cancel : '', //  
                        show:false,    //
                        btnDoub:true , // 
                        defaultShow:true,//false
                        msgCon:'' //
                    }
                }
            }
        }
    }
</script>
<style lang="less" scoped>
    #mask{
        position: fixed;
        top:0;
        left:0;
        width:100%;
        height:100%;
        background:rgba(0,0,0,0.4);
        z-index:300;
        .title{
            line-height: 80px;
            font-size:36px;
        }
        .con{
            width:85%;
            height:auto;
            background:#fff;
            margin:auto;
            margin-top:35%;
            padding-top:30px;
            text-align: center;
            border-radius: 10px;
            padding-bottom:10px;
            position: relative;
            .closeIcon{
                float:left;
                position: fixed;
                right:80px;
                top:280px;
                width:50px;
                height:50px;
                img{
                    width:100%;
                    height:auto;
                }
            }
            ul{
                padding:30px;
                box-sizing: border-box;
                li{
                    line-height: 55px;
                    font-size: 34px;
                }
            }
            img{
                width:25%;
                height:auto;
            }
        }
    }
    .foot-btn{
        width:100%;
        height:80px;
        border-top:2px solid #ccc;
        background:#fff;
        margin-top:20px;
        .border{
            border-right:1PX solid #ccc;
        }
        .green{
            color:#76da74
        }
        button{
            float:left;
            width:50%;
            line-height: 80px;
            background:#fff;
            font-size: 34px;
        }
        .sizeBtn{
            width:100%;
            text-align: center;
            color:#f03232;
        }
    }
    .btn{
        font-size:36px;
        width: 100%;
        text-align: center;
        display: block;
        margin:auto;
    }
    .van-button--large{
        height:100px;
    }
</style>


